import React from 'react';
import PropsTypes from 'prop-types';
import {Input} from './Input';

/**
 * 
 */
export class FileInput extends React.Component{
    state = {
        isFileAPISupported: true
    }
    
    reader = null;

    componentDidMount () {
        if (!(window.File &&
            window.FileReader &&
            window.FileList &&
            window.Blob)) {
                this.setState({
                    isFileAPISupported: false
                });

                return;
        }

        this.reader = new FileReader();

    }

    onFileInputChange = (e) => {
        const file = e.target.files[0];

        this.reader.onload = (e) => {
            console.log('read');
            console.dir(file);
            console.dir(e);
        };

        this.reader.readAsText(file);
        // todo remove console
        console.dir(files[0]);
    }

    render () {
        const {isFileAPISupported} = this.state;
        const {messageNoBrowserSupport} = this.props;

        return (
            isFileAPISupported ? <Input
                id={this.props.id}
                type={'file'}
                onInputChange={this.onFileInputChange}
            /> : messageNoBrowserSupport
        );
    }
}

FileInput.propsTypes = {
    messageNoBrowserSupport: PropsTypes.string,
    manageFileInput: PropsTypes.func.isRequired
}

FileInput.defaultProps = {
    messageNoBrowserSupport: 'Your browser has no support for Uploading Files. Use another browser.',
    manageFileInput: () => {}
}